import { Component, Element, h, Host, Prop } from '@stencil/core'

@Component({
  tag: 'ato-col',
  shadow: false,
})
export class AtoCol {
  @Prop() size?: 'auto' | string
  @Prop() sizeSm?: string
  @Prop() sizeMd?: string
  @Prop() sizeLg?: string
  @Prop() sizeXl?: string
  @Prop() offset?: string
  @Prop() offsetSm?: string
  @Prop() offsetMd?: string
  @Prop() offsetLg?: string
  @Prop() offsetXl?: string
  @Prop() push?: string
  @Prop() pull?: string

  @Element() private element!: HTMLElement

  componentDidLoad() {
    const ionCol = document.createElement('ion-col')
    const props = {
      size: this.size,
      'size-sm': this.sizeSm,
      'size-md': this.sizeMd,
      'size-lg': this.sizeLg,
      'size-xl': this.sizeXl,
      offset: this.offset,
      'offset-sm': this.offsetSm,
      'offset-md': this.offsetMd,
      'offset-lg': this.offsetLg,
      'offset-xl': this.offsetXl,
      push: this.push,
      pull: this.pull,
    }

    for (const [key, value] of Object.entries(props)) {
      if (value) {
        ionCol.setAttribute(key, value)
      }
    }

    const parent = this.element.parentElement
    if (parent) {
      parent.replaceChild(ionCol, this.element)
      ionCol.appendChild(this.element.childNodes[0])
    }
  }

  render() {
    return (
      <Host>
        <slot />
      </Host>
    )
  }
}
